// homepage

// * header
.header-homepage {
  width: auto;
  height: 603px;
  background: $color-corduroy no-repeat 100% 0
    url("../assets/img/bg-homepage.svg");

  // * nav
  .navbar {
    &__brand > img {
      color: $color-white;
    }

    &-collapse {
      background-color: $color-corduroy;
    }

    &__menu-left,
    &__menu-right {
      .navbar__link {
        color: $color-white;
        @include transition(all 0.3s ease-in-out);
        &:hover {
          @include transition(all 0.3s ease-in-out);
          color: $color-rangoongreen;
        }
      }
    }

    .burger-toggler div {
      background-color: #fff;
    }
  }

  // * hero
  .homepage {
    .container {
      padding: 0;
    }
    &__heading {
      color: $color-white;
    }
    &__header {
      color: $color-white;
    }
    &__text {
      padding-top: 13px;
    }
    &__trending__left {
      p {
        color: $color-shipgray;
      }
      a {
        color: $color-blazeorange;
      }
    }
  }
}

.homepage__header__image {
  position: absolute;
  top: 143px;
  right: 0;
}

// * main
.homepage {
  // * trending

  &__trending {
    display: flex;
    height: 247px;
    padding: 81px 0 86px 0;
    background-color: $color-geyser;

    &__left {
      padding-right: 35px;

      p {
        padding-top: 14px;
        color: $color-shipgray;
        white-space: nowrap;
      }
      a {
        color: $color-blazeorange;
        text-decoration: underline;
        text-underline-position: under;
        padding-bottom: 1px;
        white-space: nowrap;

        &:hover {
          text-decoration: underline;
        }
      }
    }

    // how works-buttons
    &__right {
      display: flex;
      a {
        margin: 0 8px 8px 0;
        color: $color-midgray;
        text-decoration: none;
        @include transition(all 0.3s ease-in-out);
        &:hover {
          color: $color-white;
          background-color: $color-corduroy;
        }
      }
    }
  }

  // * how works

  &__how-works {
    height: 286px;
    width: 100%;
    padding: 139px 0 116px 0;
    &__text {
      color: $color-mountainmist;
      white-space: nowrap;
    }
    ul {
      display: flex;
      list-style: none;
    }
    &__item {
      display: flex;
      margin: 5px 0;
      white-space: nowrap;
      padding: 0 30px;
      p {
        padding-top: 2px;
      }
      .profile {
        color: $color-black;
      }
      .oval-right {
        margin-top: 5px;
        padding-right: 20px;
        font-size: $font-12;
        color: $color-spunpearl;
      }
      .number {
        margin-bottom: 5px;
        padding: 0 25px 0 50px;
        color: $color-blazeorange;
      }
    }
  }

  // * daily

  &__daily {
    height: 1181px;
    width: 100%;
    &__top {
      display: flex;
      width: 100%;
      &__heading {
        h2 {
          padding-right: 200px;
        }
      }
      &__category {
        display: flex;
        align-items: center;
        button {
          padding: 0 0 0 5px;
          box-shadow: none;
          outline: none;
        }
      }
    }

    &__explore {
      padding: 34px;
      margin: auto;
      white-space: nowrap;
      div.number {
        padding-bottom: 15px;
        color: $color-haiti;
      }
      a {
        text-underline-position: under;
      }
      &__category {
        .sort-by {
          margin-left: 30px;
        }
        a {
          padding-left: 5px;
          color: $color-shipgray;
          @include transition(all 0.3s ease-in-out);
          &:hover {
            @include transition(all 0.3s ease-in-out);
            color: $color-black;
          }
        }
        img {
          width: 24px;
          height: auto;
          transform: rotate(270deg);
          text-decoration: none;
        }
      }
    }
  }

  // * testimonial

  &__testimonial {
    height: 818px;
    position: relative;
    background: no-repeat 0 0 url("../assets/img/bg-testimonial.svg");

    &__blockquote-card {
      position: relative;

      #carousel-homepage {
        background: no-repeat url("../assets/img/testimonial-dummy.png");
        padding: 55px;

        // carousel-card and background
        .carousel-inner {
          width: 50.5%;
          height: auto;
        }

        // carousel-indicators
        .carousel-indicators {
          display: flex;
          position: absolute;
          width: 20%;
          z-index: 10;
          li.active {
            background-color: $color-spunpearl;
          }
          li {
            width: 38px;
            height: 4px;
            background-color: $color-bonjour;
          }
        }

        // carousel-control buttons
        .controls {
          .carousel-control-prev,
          .carousel-control-next {
            width: 48px;
            height: 48px;
          }
        }
      }
    }

    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      top: 120px;
      left: 0;
      z-index: -50;
      background-color: rgb(248, 248, 250);
    }
  }

  // * clients

  &__clients {
    background: $color-aquahaze no-repeat url("../assets/img/clients-bg.jpg");

    &__heading {
    }
    &__body {
    }
    &__post-job {
      p {
        color: $color-shipgray;
      }
      a {
        color: $color-blazeorange;
      }
    }
    &__cards {
    }
    &__text {
      color: $color-shipgray;
    }
    &__bottom {
      &--logos {
        ul {
          display: flex;
        }
        li {
          list-style: none;
        }
      }
      p {
        text-align: center;
      }
    }
  }

  // clients-bg color
  .jumbotron {
    padding: 0;
    background-color: $color-aquahaze;
  }

  // * find job

  &__find-job {
    display: flex;

    &__title {
      button {
        color: $color-black;
      }
    }
    &__btn {
      display: flex;
    }
  }
}

