// footer

.footer {
  // padding-bottom: 44px;
  &__brand {
    img {
      color: $color-coral;
    }
  }
  &__list > li > p {
    color: $color-shipgray;
  }
  &__list > li > a {
    color: $color-midgray;
  }

  &__language-bar {
    color: $color-midgray;
    display: flex;
    margin-left: auto;
    padding-right: 30px;
    p {
      padding-top: 5px;
      padding-right: 20px;
    }
    .dropdown-menu,
    button {
      margin-top: -20px;
      color: $color-midgray;
      background-color: transparent;
      border: 1px solid $color-bonjour;
      border-radius: 10px;
      &:hover,
      &:focus,
      &:active {
        background-color: $color-white;
      }
      .dropdown-menu {
        padding: 0;
        margin-top: 16px;
        background-color: $color-white;
      }

      .dropdown-item {
        background-color: $color-white;
        padding: 8px 0 5px 0;
        width: 50px;
        &:hover {
          background-color: $color-geyser;
        }
      }
    }
  }

  &__bottom-links ul li a {
    text-decoration: none !important;
  }

  &--sm {
    height: 64px;
    background-color: $color-white;
    .row {
      display: flex;
    }

    &__bottom-links > ul {
      display: flex;
      li {
        a {
          @include transition(all 0.3s ease-in-out);
          &:hover {
            @include transition(all 0.3s ease-in-out);
            color: $color-rangoongreen;
            text-decoration: none;
          }
        }
      }
    }
    &__bottom-links a {
      color: $color-midgray;
    }
  }

  &--lg {
    &__brand {
      display: flex;
      flex-direction: column;
      padding: 7px 0 0 1px;
      img {
        width: 81px;
        height: 24px;
      }
      span {
        padding-top: 32px;
        color: $color-midgray;
      }
    }
    .footer__links {
      padding-bottom: 5px;
    }

    .footer__list {
      white-space: nowrap;

      li {
        padding: 5px;
      }

      p {
        margin: 0;
      }

      li > a {
        text-decoration: none;
        @include transition(all 0.3s ease-in-out);
        &:hover {
          @include transition(all 0.3s ease-in-out);
          color: $color-rangoongreen;
        }
      }
    }
    &__bottom {
      display: flex;
      padding-top: 19px;
      .footer__social-icons {
        a {
          text-decoration: none;
          padding-right: 12px;
          img {
            margin: auto;
          }
        }
      }
    }

    &__bottom-links {
      display: flex;
      margin-left: 5%;
      a {
        color: $color-midgray;
        &:hover {
          text-decoration: none;
        }
      }
    }
    &__bottom-links > ul {
      display: flex;
      li {
        padding: 6px 24px;
        a {
          @include transition(all 0.3s ease-in-out);
          &:hover {
            @include transition(all 0.3s ease-in-out);
            color: $color-rangoongreen;
          }
        }
      }
    }
  }
}

// * responsive

// Extra Small devices (phone, 320px and up)

@media (min-width: 320px) and (max-width: 767px) {
  .footer {
    margin: 0 20px;

    &--sm {
      .row {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 8px;
      }

      &__brand {
        display: flex;
        justify-content: center;
        width: 100%;
        padding: 2px 0 14px 0;
      }

      &__bottom-links {
        display: flex;
        justify-content: center;
        width: 100%;
        margin: 0;
        ul {
          justify-content: space-between;
        }
        li {
          white-space: nowrap;
          margin: 0 6px;
          a {
            padding-left: 0;
          }
        }
      }
    }

    &--lg {
      .footer__list {
        li {
          padding: 0 12px;
          a {
            white-space: nowrap;
          }
        }

        li p {
          padding-bottom: 10px;
        }
      }

      &__bottom-sm {
        display: flex;
        flex-direction: column;

        .footer__social-icons-sm > a {
          padding: 0 5px 0 0;
        }

        &-links {
          padding: 16px 0 0 0;
        }

        &-links ul li {
          padding: 0 2px;

          a {
            color: #64626a;
          }
        }

        .footer__language-bar-sm {
          margin-top: 12px;
        }

        .footer--lg__brand-sm {
          display: flex;
          flex-direction: row;
          align-items: center;
          width: 100%;
          padding: 0;
          margin-bottom: 20px;

          img {
            float: left;
          }

          span {
            margin-left: auto;
          }
        }

        .list-unstyled li {
          padding: 0;
        }
      }

      .border-footer-wrapper {
        color: #868686;
        position: relative;
        margin-bottom: 20px;

        .border-footer {
          content: "";
          display: block;
          position: absolute;
          left: -6.5%;
          width: 110.5%;
          background-color: $color-bonjour;
          height: 1px;
        }
      }
    }
  }
}

// Medium devices (tablets, 768px and up)

@media (min-width: 768px) {
  .footer {
    margin: 0;
    &__links {
      .row > div {
        padding: 0;
        margin: 0 7px;
        .md-margin {
          width: 100%;
          margin-left: 74%;
          padding-top: 50px;
        }
      }
    }

    &__social-icons {
      margin-left: -20px;
    }

    &--sm {
      height: 64px;

      &__bottom-links {
        margin-left: auto;
      }
      &__bottom-links > ul {
        li {
          padding-top: 6px;
          a {
            padding-left: 48px;
          }
        }
      }
    }

    &--lg {
      &__bottom {
        padding-bottom: 22px;
      }
      &__bottom-links > ul {
        li {
          margin-left: -16px;
          padding: 6px 20px;
        }
      }
      .footer--lg__brand-sm {
        display: none;
      }

      .border-footer-wrapper {
        color: #868686;
        position: relative;
        margin-bottom: 20px;

        .border-footer {
          content: "";
          display: block;
          position: absolute;
          left: -6.5%;
          width: 111%;
          background-color: $color-bonjour;
          height: 1px;
        }
      }
    }
  }
}

// Large devices (desktops, 992px and up)

@media (min-width: 992px) {
  .footer {
    margin: 0 0 44px 0;

    &__links {
      .row > div {
        padding: 0;
        margin: 0;
        .md-margin {
          margin-left: 0;
          padding-top: 0;
        }
      }
    }

    &--sm {
    }

    &--lg {
      &__bottom-links > ul {
        li {
          margin-left: 0;
          padding-right: 25px;
        }
      }
      &__bottom {
        padding: 10px 0 0 0;
      }

      .border-footer-wrapper {
        margin-bottom: 24px;
      }
    }
  }
}

